body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Grid_wrapper {
  display: -webkit-flex;
  display: flex;
  max-width: 1200px;
  margin: 0px auto; }

.Grid_3Col {
  display: grid;
  grid-template-columns: 250px minmax(300px, 500px) minmax(250px, 450px);
  grid-column-gap: 10px; }

.Grid_3Col .Column:nth-child(3) {
  height: 100vh;
  overflow: scroll;
  position: -webkit-sticky;
  position: sticky;
  top: 60px; }

.Grid_2Col {
  display: grid;
  grid-template-columns: 250px minmax(550px, 950px);
  grid-column-gap: 10px; }

.Column2 {
  margin-right: 20px; }

.Grid_2Col_Container {
  display: grid;
  grid-column-gap: 10px;
  grid-template-columns: minmax(300px, 500px) minmax(250px, 450px); }

.Grid_2Col_Container .Column:nth-child(2) {
  height: 90vh;
  overflow: scroll;
  position: -webkit-sticky;
  position: sticky;
  top: 60px;
  margin-top: 10px; }

.lightest_gray {
  background: #f4f4f4; }

.light_gray, .TargetDetails, .Card, .TreeBranchGroup, .TextBox {
  background: #fcfafac9; }

.light_gray_border, .TreeBranchGroup {
  border: solid thin #d1cfcf; }

.light_gray_font, .ThumbnailList .controls {
  color: #b6b5b5; }

.dark_gray_opacity, .treeItem, .treeItem-group {
  background: rgba(40, 44, 42, 0.8); }

.medium_gray_opacity, .leftNav li:hover, .PillItemActive {
  background: rgba(40, 44, 42, 0.562); }

.gray_font, .infoCard, .imgPost .controls, .Branch_title, .PageHeader, .CommentSectionWrapper .Comment div:nth-child(3), h3, .imgUploadBtn {
  color: gray; }

.wittwer_blue_font {
  color: #00aeef; }

.wittwer_blue {
  background: #00aeef; }

.lightest_green {
  background: #e7ecea; }

.mid_green_font {
  color: #2CC185; }

.mid_green {
  background: #2CC185; }

.r_body_gray {
  background: #e6e4e4; }

.dark_gray_font, .imgPost .controls .Name {
  color: #282828; }

.dark_gray_background {
  background: #282828; }

.wittwer_purple_font {
  color: #4055bd; }

.wittwer_purple_background {
  background-color: #4055bd; }

.leftNav li {
  -webkit-transition: background-color 0.2s ease;
  transition: background-color 0.2s ease;
  padding: 10px; }

.leftNav li:hover {
  color: white; }

.infoTitle {
  font-size: 20px; }
  .infoTitle i {
    margin-right: 10px; }

.label {
  font-weight: bold; }

.treeItem {
  display: grid;
  grid-template-columns: 25% 55% 20%;
  padding: 5px;
  border-radius: 10px;
  color: white; }
  .treeItem .box {
    padding: 5px; }

.treeItem div:nth-child(3) {
  color: aqua; }

.treeItem-group {
  display: grid;
  grid-template-columns: 25% 50% 20%;
  padding: 5px;
  border-radius: 10px;
  color: white;
  max-width: 185px; }
  .treeItem-group .box {
    padding: 5px; }
    .treeItem-group .box a {
      color: white; }

.treeItem-group div:nth-child(3) {
  color: aqua; }

.imgPost {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column; }
  .imgPost .controls {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    padding: 8px; }
    .imgPost .controls i {
      margin-right: 5px; }
  .imgPost .img_container img {
    width: 100%; }
  .imgPost .gallery_indicator {
    position: relative; }
    .imgPost .gallery_indicator span {
      position: absolute;
      top: 5px;
      right: 2px;
      z-index: 100;
      color: aqua; }
    .imgPost .gallery_indicator i {
      float: right;
      padding-right: 10px; }

.TreeBranchWrapper {
  display: grid;
  grid-template-columns: repeat(auto-fill, 390px);
  -webkit-justify-content: center;
          justify-content: center;
  grid-gap: 1em; }

.TreeBranchGroup {
  border-radius: 10px; }

.Branch_title {
  padding: 5px;
  text-transform: uppercase;
  font-weight: bold; }

.TreeBranchGroup_Parent {
  display: grid;
  grid-template-columns: auto auto; }
  .TreeBranchGroup_Parent .Member {
    margin: 1px; }

.TreeBranchGroup_Child {
  display: grid;
  grid-template-columns: auto auto auto; }

.TreeBranchGroup_start,
.TreeBranchGroup_Child_center,
.TreeBranchGroup_Child_end {
  display: grid;
  margin: 3px; }

.TreeBranchGroup_start {
  -webkit-justify-content: start;
          justify-content: start; }

.TreeBranchGroup_Child_center {
  -webkit-justify-content: center;
          justify-content: center; }

.TreeBranchGroup_Child_end {
  -webkit-justify-content: end;
          justify-content: end; }

.ThumbnailList_Wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-column-gap: 10px; }

.ThumbnailList {
  min-width: 250px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column; }
  .ThumbnailList .controls {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    padding: 8px; }
  .ThumbnailList .img_container img {
    width: 100%; }

.HeaderWrapper {
  width: 100%;
  -webkit-align-items: center;
          align-items: center;
  height: 70px; }

.HeaderWrapper div:nth-child(2) {
  margin-right: 30px; }

.PageHeader {
  display: -webkit-flex;
  display: flex;
  padding: 10px;
  font-size: 20px; }

.PillList {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap; }
  .PillList div {
    margin: 3px;
    padding: 5px;
    border-radius: 20px;
    color: white;
    font-size: 12px; }

.PillItem {
  background: #ee6363; }

.TargetDetails {
  padding: 10px;
  margin: 0px 0px 10px 0px; }
  .TargetDetails .Thumbnail img {
    width: 70%; }
  .TargetDetails h3 {
    margin: 10px 5px 5px 5px; }

.myTextArea {
  background: yellow !important;
  color: red; }

.EditNav {
  position: fixed;
  z-index: 100;
  overflow-y: hidden;
  top: 20;
  right: 0;
  width: 400px;
  height: 100vh;
  margin: 0;
  list-style: none;
  font-size: 15px;
  background: white;
  padding: 10px;
  border-left: solid thin #d4d4d4;
  -webkit-transition: all .25s ease;
  transition: all .25s ease; }

.Hidden {
  width: 0px;
  position: fixed;
  z-index: 100;
  overflow-y: hidden;
  top: 20;
  right: 0;
  -webkit-transition: all .25s ease;
  transition: all .25s ease;
  height: 100vh; }

.CommentSectionWrapper {
  border: solid thin #d1d0d0;
  padding: 5px; }
  .CommentSectionWrapper .Comment {
    display: -webkit-flex;
    display: flex; }
    .CommentSectionWrapper .Comment div {
      padding: 3px; }

.CommentSectionWrapper .Comment div:nth-child(1) {
  display: -webkit-flex;
  display: flex; }

.CommentSectionWrapper .Comment div:nth-child(2) {
  display: -webkit-flex;
  display: flex; }

.CommentSectionWrapper .Comment div:nth-child(3) {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: flex-end;
          align-items: flex-end; }

.TextBox .Title {
  color: orange;
  font-size: 25px; }

.instructionBox {
  margin-top: 10px; }

